:root {
  font-family: $font-body;
  line-height: 1.6;
  font-weight: 400;
  color: $color-dark-text;
  background-color: $color-main-background;
  box-sizing: border-box;
}

.container {
  max-width: $container;
  margin: 0 auto;
  padding: 0 20px;
}

.error-message {
  color: #ff4d4d;
  font-size: 1.4rem;
  margin-bottom: 15px;
}

// NOTE: custom select
.custom-select {
  padding: 5px 10px;
  border-color: $color-primary-purple;
  background-color: #e6f7f5;
  color: #00c4b4;
  &:focus {
    outline: none;
    box-shadow: none;
    border-color: $color-primary-purple;
  }
  & option {
    background-color: white;
    color: black;
  }
}

// NOTE: form
.block__form {
  margin-bottom: 15px;
  width: 100%;
  .form__field {
    width: 100%;
    text-align: left;
    margin-bottom: 15px;

    label {
      display: block; /* Đảm bảo label không bị dính vào input */
      margin-bottom: 5px;
      font-weight: 500;
    }

    &-input {
      border: 1px solid #a5a5a5;
      border-radius: 8px;
      overflow: hidden;
      @include flexbox(center, space-between, row, flex, nowrap);
      width: 100%;
      input {
        width: 100%;
        padding: 12px;
        border: none;
        background: #fff;
        box-sizing: border-box;
      }
      .see-password {
        width: 16px;
        height: 16px;
        border: none;
        padding: 22px;
        display: flex;
        align-items: center;
        justify-content: center;
        background: transparent;
        border-top-left-radius: 0;
        border-bottom-left-radius: 0;
        border-left: 1px solid #a5a5a5;

        .icon-eye {
          font-size: 1.9rem;
          margin: 0;
          position: absolute;
        }
      }
    }
  }
}